.container {
    display: flex;
    background-color: #11E6AA;
    justify-content: center;
    align-items: center;
    
}

.login {
    display: flex;
    flex-direction: column;
    flex : 1;
    height: 100vh;
    justify-content: flex-start;
    align-items: center;
    background-color: black;
    position: relative;
}

.slogan {
    font-family : Rubik;
    color: beige;
    text-align: center;
    padding-right: 150px;
    padding-left: 150px;
    margin-top : 5vh;
}

.info {
    position: absolute;
    bottom : 35px;
    display : flex;
    justify-content: center;
    align-items: center;
}

.info-text {
    color : rgb(112, 122, 122);
    font-family : Rubik;
    font-size: large;
    text-align: center;
}

.link-text {
    font-family : Rubik;
}

.animation {
    flex : 1;
    height: 100vh;
    background-color: "black" 
}
.fade-in { 
    animation: fadeIn 8s;
    -webkit-animation: fadeIn 8s;
    -moz-animation: fadeIn 8s;
    -o-animation: fadeIn 8s;
    -ms-animation: fadeIn 8s;
    animation-delay: initial;
 }

 .button-fade-in {
    animation: fadeIn 3s;
    animation-delay: inherit;
    opacity: inherit;
 }


 .logo {
    height: 80px;
    width: 80px;
    margin-right: 25px;
    background-size: 100%;
    background-image: url(../../../assets/images/icon.png);
 }

 .brand{
    color : bisque;
    font-family : Rubik;
    font-size: 100px;
 }

 .header {
    margin-top: 25px;
    margin-bottom: 25px;
    display : flex;
    justify-content: center;
    align-items: center;
 }



@keyframes fadeIn {
    0% { opacity: 0; }
    100% { opacity: 1; }
  }